.skillsBlock {
    min-height: 50vh;
    height: auto;
    min-width: 360px;
    border: 1px solid black;
    background-color: #ffbda1;
}

.skillsConteiner {
    border: 1px solid black;
    min-height: 50vh;
    width: 80%;
    margin: 5% auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

}

.title {
    background-color: orange;
}

.skills {
    border: 1px solid;
    background-color: #ffb1e6;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}

